<template>
    <div class="login">
        <div class="contain">
            <div class="left">
                <img :src=this.imgUrl alt="">
            </div>
            <div class="right">
                <div>用户名：<el-input v-model="input" placeholder="请输入用户名"></el-input>
                </div>
                <div>密码: <el-input placeholder="请输入密码" v-model="input" show-password></el-input>
                </div>
                <div class="button">
                    <el-button type="primary">登录</el-button>
                    <el-button type="primary">注册</el-button>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    name: 'login',
    data() {
        return {
            imgUrl: "https://cdn.jsdelivr.net/gh/Tony-hurry/imageBad@main//study-images/linux01.png",
            passwd: 'tony',
            name: '1234'
        }
    }
}
</script>
<style scoped lang="less">
.login {
    width: 100vw;

    .contain {
        display: flex;

        .left {
            flex: 3;

            img {
                width: 100%;
                height: 100%;
            }
        }

        .right {
            padding: 5px;
            right: 2;
            flex: 2;
            display: flex;
            flex-direction: column;
            justify-content: space-around;

            .button {
                display: flex;
                justify-content: space-between;
            }
        }
    }
}
</style>


