<template>
    <div id="headpage">
        <pictures-show :pictures='pictures' />
        <HeadGetInfo />
        <Login></Login>
    </div>
</template>
<script>

import PicturesShow from 'components/common/picturesshow/PicturesShow';


import HeadGetInfo from './childpage/HeadGetInfo'
import Login from './childpage/login'


export default {
    name: 'HeadPage',
    data() {
        return {
            pictures: [
                require('assets/img/imagesshow/wallpaper01.jpg'),
                require('assets/img/imagesshow/wallpapers02.png'),
                require('assets/img/imagesshow/wallpapers03.png'),
                require('assets/img/imagesshow/wallpapers04.png'),
            ]
        }
    },
    components: {
        PicturesShow,
        HeadGetInfo,
        Login
    }
}
</script>
