<template>
    <div class="getinfo">
        <el-table :data="tableData" :border="true" style="width: 100%">
            <el-table-column prop="date" label="日期" width="180">
            </el-table-column>
            <el-table-column prop="name" label="姓名" width="180">
            </el-table-column>
            <el-table-column prop="address" label="地址">
            </el-table-column>
        </el-table>
    </div>
</template>
<script>
import { user } from 'data/headblog'
export default {
    name: 'headgetinfo',
    data() {
        return {
            person: {},
            tableData: [{
                date: '2016-05-02',
                name: '王小虎',
                address: '上海市普陀区金沙江路 1518 弄'
            }]
        }
    },
    created() {
        /* this.person = new headblog() */
    },
    methods: {
        getinfo() {
            let info = this.$refs.text
            this.person = new user(info[0].value, info[1].value, info[2].value)
        }

    }
}
</script>
