<template>
    <div class="picturesshow" >
        <div class="picitem" v-for="item in pictures">
            <img :src="item" alt="">
        </div>
    </div>
</template>
<script>
export default {
    name: 'picturesshow',
    props:{
        pictures:{
            type:Array,
            default(){
                return []
            }
        }
    }
}
</script>
<style scoped>
.picturesshow{
    width:100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    border: 2px solid rgba(100, 100, 100, .2);
    border-radius: 4px;
}
.picturesshow .picitem{
    width: 45%;
    padding:5px;
    overflow: hidden;
    border-radius: 4px; 
}
.picturesshow .picitem img{
    width:100%;
}
</style>


